<template>
    <div class="container text-center p-5">
        <h2 class="tittle">PROJECTS</h2>
        <div id="carouselExampleCaptions" class="carousel slide">
            <div class="carousel-indicators">
                <!-- <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button> -->
                <button v-for="(project, index) in projectsWithSubcollections" :key="index" :project="project" type="button" data-bs-target="#carouselExampleCaptions" :data-bs-slide-to="index" :class="{ active: index === 0 }" aria-label="Slide {{ index + 1 }}"></button>
            </div>
            <div class="carousel-inner">
                <DataProjectsComp v-for="(project, index) in projectsWithSubcollections" :key="index" :project="project" :is-active="index === 0"/>
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
            </button>
        </div>
    </div>
</template>

<style scope>
    .tittle{
        color: white;
        font-family: FuenteUno;
        font-size: 50px;
    }
</style>

<script>
    import db from '../../utils/firebase'
    import { collection, getDocs, query, where, onSnapshot } from "firebase/firestore"; // Importa onSnapshot para escuchar cambios en Firestore
    import { onMounted, reactive, ref } from 'vue';
    import DataProjectsComp from './projectData/DataProjectsComp.vue';

    export default {
            components: {
                DataProjectsComp
            },
            setup() {
        const projectsWithSubcollections = ref([]);

        // Utiliza onMounted para cargar los datos iniciales de la colección
        onMounted(async () => {
            try {
            await fetchProjectsData();
            } catch (error) {
            console.error('Error fetching initial projects data:', error);
            }
        });

        // Utiliza onMounted para suscribirse a cambios en la colección
        onMounted(() => {
            const unsubscribe = onSnapshot(collection(db, 'projects'), async () => {
            try {
                await fetchProjectsData();
            } catch (error) {
                console.error('Error fetching projects data:', error);
            }
            });

            // Retorna una función de limpieza para detener la escucha de cambios cuando sea necesario
            return unsubscribe;
        });

        // Función para cargar los datos de los proyectos y sus subcolecciones
        async function fetchProjectsData() {
            const projectsArray = [];
            const projectsQuerySnapshot = await getDocs(collection(db, 'projects'));
            
            for (const projectDoc of projectsQuerySnapshot.docs) {
            const projectWithData = {
                project: projectDoc.data(),
                subcollections: {}
            };
            
            const subcollectionsQuerySnapshot = await getDocs(collection(projectDoc.ref, 'projectData'));

            subcollectionsQuerySnapshot.forEach(subcollectionDoc => {
                projectWithData.subcollections[subcollectionDoc.id] = subcollectionDoc.data();
            });

            projectsArray.push(projectWithData);
            }

            projectsWithSubcollections.value = projectsArray;
        }

        return {
            projectsWithSubcollections
        };
        }
    }
</script>
