<template>
    <div class="container text-center p-5">
        <h2 class="tittle">EXPERIENCE</h2>
        <div>
            
        </div>
    </div>
    <div>
        <ExperienceComp v-for="(job, index) in jobs" :key="index" :job="job.data()"/>
    </div>
</template>
  
<script>
import db from '../../utils/firebase'
import { collection, getDocs, onSnapshot } from "firebase/firestore"; // Importa onSnapshot para escuchar cambios en Firestore
import { onMounted, reactive, ref } from 'vue';
import ExperienceComp from './experienceData/DataExperienceComp.vue';

export default {
    components: {
        ExperienceComp
    },
    setup() {
        // Define una variable reactiva para almacenar los documentos de Firestore
        const jobs = ref([]);

        // Utiliza el hook onMounted para cargar los datos cuando el componente está montado
        onMounted(async() => {
            try {

                // Escucha cambios en la colección "experience" en Firestore
                const unsubscribe = onSnapshot(collection(db, 'experience'), (snapshot) => {
                    // Limpia el array antes de agregar los nuevos documentos
                    jobs.value = [];
                    snapshot.forEach(doc => {
                        jobs.value.push(doc);
                    });
                });
                // Retorna la función unsubscribe para detener la escucha cuando sea necesario
                return unsubscribe;
            } catch (error) {
                console.error('Error al obtener datos de Firestore:', error);
            }
        });

        // Retorna las variables y métodos que quieres exponer en el template
        return {
            jobs
        };
    }
}
</script>

<style scoped>
    .tittle{
        color: white;
        font-family: FuenteUno;
        font-size: 50px;
    }
</style>