<template>
    <div class="container main-container d-flex justify-content-center">

        <div class="block">
            <!-- Renderiza los datos del documento recibido como propiedad -->
            <!-- <p style="color: black;">{{ city.name }}, {{ city.state }}, {{ city.country }}</p> -->
            <div class="row">
                <div class="col-md-6">
                    <div class="row">
                        <h4 class="company">{{job.company}}</h4>
                        <h5 class="position">{{job.position}}</h5>
                    </div>
                </div>
                <div class="col-md-6">
                    <div class="row">
                        <h4 class="place">{{job.place}}</h4>
                        <h5 class="dates">{{job.dateStart}} to {{job.dateEnd}}</h5>
                    </div>
                </div>
            </div>
            <div>
                <ul style="color:white;">
                    <li v-for="(task, index) in tasksArray" :key="index">{{ task }}</li>
                </ul>
            </div>
            
        </div>
        
    </div>
</template>
  
<script>
    export default {
        props: {
            job: {
            type: Object,
            required: true,
            validator: value => {
                // Validar la estructura del objeto recibido
                return (
                value.hasOwnProperty('company') &&
                value.hasOwnProperty('dateEnd') &&
                value.hasOwnProperty('dateStart') &&
                value.hasOwnProperty('place') &&
                value.hasOwnProperty('position') &&
                value.hasOwnProperty('tasks')
                );
            }
            }
        },
        computed: {
            // Separar las tareas del objeto job y convertirlas en un array usando split(',')
            tasksArray() {
            return this.job.tasks.split(',').map(task => task.trim());
            }
        }
    };
</script>

<style scope>
.main-container{
    padding-bottom: 10px; /* Espacio interno para separar el contenido del borde */
}
.block{
    max-width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px 15px 0px 15px;
    background-color: rgba(255, 255, 255, 0.164);
    border-radius: 15px;
}
.company{
    color: white;
    font-family: FuenteUno;
}
.position{
    color:white;
    font-family: FuenteUno;
}
.place{
    display: flex;
    color: white;
    justify-content: end;
    font-family: FuenteUno;
}
.dates{
    display: flex;
    color: white;
    justify-content: end;
    font-family: FuenteUno;
}
.tasks{
    color: white;
    font-family: FuenteUno;
}
</style>